<template>
  <view class="case-num">
    <view class="title-box">
      <view class="title">{{scope}}疫情</view>
      <text class="time">截至 {{ isShow ? formatDate(epidemic.time) : epidemic.time}}</text>
    </view>
    <view class="num-box">
      <view class="count">
        <view class="count-compare">较昨日<text style="color: rgb(247,76,49);">{{formatNum(epidemic.addConfirm)}}</text></view>
        <view class="count-num" style="color: rgb(247,76,49);">{{epidemic.nowConfirm}}</view>
        <view class="count-name">现存确诊</view>
      </view>
      <view class="count">
        <view class="count-compare">较昨日<text style="color: rgb(247,130,7);">{{formatNum(epidemic.addWzz)}}</text></view>
        <view class="count-num" style="color: rgb(247,130,7);">{{epidemic.nowWzz}}</view>
        <view class="count-name">现存无症状</view>
      </view>
      <view class="count">
        <view class="count-compare">较昨日<text style="color: rgb(93,112,146);">{{formatNum(epidemic.addDead)}}</text></view>
        <view class="count-num" style="color: rgb(93,112,146);">{{epidemic.totalDead}}</view>
        <view class="count-name">累计死亡</view>
      </view>
      <view class="count">
        <view class="count-compare" v-show="isShow">较昨日<text style="color: rgb(174,33,44);">{{formatNum(epidemic.addTotalConfirm)}}</text></view>
        <view class="count-num" style="color: rgb(174,33,44);">{{epidemic.totalConfirm}}</view>
        <view class="count-name">累计确诊</view>
      </view>
      <view class="count">
        <view class="count-compare" v-show="isShow">较昨日<text style="color: rgb(40,183,163);">{{formatNum(epidemic.addHeal)}}</text></view>
        <view class="count-num" style="color: rgb(40,183,163);">{{epidemic.totalHeal}}</view>
        <view class="count-name">累计治愈</view>
      </view>
      <view class="count">
        <view class="count-compare" v-show="isShow">较昨日<text style="color: rgb(162,90,78);">{{formatNum(epidemic.addAbroad)}}</text></view>
        <view class="count-num" style="color: rgb(162,90,78);">{{isShow ? epidemic.totalAbroad : epidemic.addAbroad}}</view>
        <view class="count-name">{{isShow ? "累计" : "新增"}}境外输入</view>
      </view>
    </view>
  </view>
</template>

<script>
  import store from '@/store/index.js'
  export default {
    name: "case-num",
    // 定义 props 属性，用来接收外界传递到当前组件的数据
    props: {
      // 疫情情况数据的对象
      epidemicData: {
        type: Object | null,
        default: {}
      },
      scope: {
        type: String,
        default: "全国"
      },
      isShow: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        
      };
    },
    created() {
      // console.log(this.epidemicData,'caseeeeeeeeeeeee')
    },
    computed: {
      // 写成计算属性便于数据改变时重新渲染组件
      epidemic(){
        return this.epidemicData || {}
      }
    },
    methods: {
      // 格式化更新时间
      formatDate(date){
        var date = new Date(date);
        var YY = date.getFullYear() + '-';
        var MM = (date.getMonth() + 1 < 10 ? "0" + (date.getMonth()+1) : date.getMonth() + 1) + "-";
        var DD = (date.getDate() < 10 ? "0" + date.getDate() : date.getDate() + "  ");
        var hh = (date.getHours() < 10 ? "0" + date.getHours() : date.getHours() + ":");
        var mm = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
        return YY + MM + DD + hh + mm;
      },
      // 格式化较昨日的数据
      formatNum(num) {
        return num >= 0 ? "+" + num : num;
      }
    }
  }
</script>

<style lang="scss" scoped>
  .case-num {
    padding: 10px;
  }
  .title-box {
    height: 24px;
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
  }
  // 更新时间
  .time {
    align-items: center;
    margin-bottom: 20px;
    font-size: 14px;
    color: #666;
  }
  // 数据容器
  .num-box {
    display: flex;
    flex-wrap: wrap;
    padding: 5px 0;
    border: 1px solid #ebebeb;
    border-radius: 12px;
    box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.06);
  }
  // 各类数据模块
  .count {
    width: 33.33%;
    text-align: center;
    padding: 10px;
    box-sizing: border-box;
    font-size: 13px;
    // 现存确诊人数
    .count-num {
      font-size: 24px;
      font-weight: bold;
      padding: 4px;
    }
    .count-name {
      font-size: 15px;
    }
  }
</style>
